import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ButtonBase from '@material-ui/core/ButtonBase';
import Typography from '@material-ui/core/Typography';
import {Redirect} from "react-router-dom";
import {Link} from 'react-router-dom';







    
    const images = [
  {
    
    title:<Link to="/aboutMe" className="MuiTypography-subtitle1 MuiTypography-colorInherit">Me</Link>,
    width: '10%', 
  
   
  },
  {
    
    title: <Link to="/proyects" className="MuiTypography-subtitle1 MuiTypography-colorInherit">Projects</Link>,
    width: '10%',
   
  },
  {
   
    title:<Link to="/contact" className="MuiTypography-subtitle1 MuiTypography-colorInherit">Contact</Link>,
    width: '10%',
   
  },
];



let open= (event) => {
 
    console.dir(event.target)
   if(event.target.textContent === "Contact"){
    return  <Redirect to ='/contact' />
    // this.props.history.push('/contact');
  //  return  this.history.push
  
   // return <Link to="/contact"></Link>
             //alert('Para más información, acuda a recepción.')
}else if
  (event.target.textContent === "Proyects"){
    return <Redirect to='/proyects' />
  }else if
  (event.target.textContent === "AboutMe"){
    return <Redirect to='/aboutMe' />
  }else{
    
  }
      
};

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    minWidth: 300,
    width: '100%',
  },
  image: {
    position: 'relative',
    height: 200,
    [theme.breakpoints.down('xs')]: {
      width: '100% !important', // Overrides inline-style
      height: 100,
      
    },
    '&:hover, &$focusVisible': {
      zIndex: 1,
      '& $imageBackdrop': {
        opacity: 0.15,
      },
      '& $imageMarked': {
        opacity: 0,
      },
      '& $imageTitle': {
        border: '4px solid currentColor',
      },
    },
  },
  focusVisible: {},
  imageButton: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: theme.palette.common.white,
    
  },
  imageSrc: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: 'cover',
    backgroundPosition: 'center 40%',
    
  },
 
  imageTitle: {
    position: 'relative',
    padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${theme.spacing(1) + 6}px`,
    
  },
  imageMarked: {
    height: 3,
    width: 18,
    backgroundColor: theme.palette.common.white,
    position: 'absolute',
    bottom: -2,
    left: 'calc(50% - 9px)',
    transition: theme.transitions.create('opacity'),
  },
  
}));

export default function ButtonBases() {
  const classes = useStyles()

  return (
    <div className={classes.root}> 
      {images.map(image => (
        <ButtonBase
          focusRipple
          key={image.title}
          className={classes.image} onClick={open}
          focusVisibleClassName={classes.focusVisible}
          style={{
            width: image.width,
          }}
        >
          <span
            className={classes.imageSrc}
            style={{
              backgroundImage: `url(${image.url})`,
            }}
          />
          <span className={classes.imageBackdrop} />
          <span className={classes.imageButton}>
            <Typography
              component="span"
              variant="subtitle1"
              color="inherit"
              className={classes.imageTitle}
            >
              {image.title}
              <span className={classes.imageMarked} />
            </Typography>
          </span>
        </ButtonBase>
      ))}
    </div>
  );
}

